import uniCalendar from '@/components/uni-calendar/uni-calendar.vue'
export default {
	components: {
		uniCalendar
	},
	data() {
		return {
			info: {
				date: '',
				startDate: '',
				endDate: '',
				lunar: true,
				range: true,
			},
			Piclist:true,
			Imglist:true,
			addpic:[],
			items: [{
					value: '首页广告',
					name: '首页广告'
				},
				{
					value: '秒杀广告',
					name: '秒杀广告',
					checked: 'true'
				},
				{
					value: '拼团广告',
					name: '拼团广告'
				},
				{
					value: '中部广告',
					name: '中部广告'
				},
				{
					value: '发现广告',
					name: '发现广告'
				},
				{
					value: '电影广告',
					name: '电影广告'
				},
			],
			current: 0
		}
	},
	methods: {
		change:function (e){
			console.log(e)
		},
		radioChange: function(evt) {
			for (let i = 0; i < this.items.length; i++) {
				if (this.items[i].value === evt.target.value) {
					this.current = i;
					break;
				}
			}
		},
		Addimg:function(e){
			uni.chooseImage({ 
			    count: 1, //默认9
			    sizeType: ['original', 'compressed'], //可以指定是原图还是压缩图，默认二者都有
			    sourceType: ['album','camera '], //从相册选择
			    success:(res)=>{  
					var tempFilePaths = res.tempFilePaths
					this.addpic = this.addpic.concat(tempFilePaths)
					if(this.addpic.length == 3){
						this.Piclist = !this.Piclist
					}
			    }
			})
		},
		deleteImage:function(index){
			uni.showModal({
				title: '提示',
				content: '确定要删除此图片吗？',
				success:(res)=>{
		        	console.log(index)
					this.addpic = this.addpic.splice(index,1)
					if(this.addpic.length < 3){
						this.Piclist = !this.Piclist
					}
				}
			})
		}
	}
}
